import VueRouter from 'vue-router'
// 引入组件
const TodoList = () => import('../pages/TodoList.vue')
const MyAbout = () => import('../pages/MyAbout.vue')
const ExerciseList = () => import('../pages/ExerciseList.vue')
const PartList = () => import('../pages/PartList.vue') 
const PartDetail = () => import('../pages/PartDetail.vue') 



// 创建并暴露一个路由器
export default new VueRouter({
	routes:[
		{
			path:'/todo',
			name:'todo',
			component:TodoList,
			meta:{
				appbar:'#0D47A1',
				exercise:true,
				title:'待办'
			}
			
		},
		
		{
			path:'/exercise',
			name:'exercise',
			component:ExerciseList,
			meta:{
				appbar:'#FFAB91',
				exercise:true,
				title:'部位'
			},
			
		},
		{
			path:'/part',
			name:'part',
			component:PartList,
			redirect:'/part/chest',
			meta:{
				appbar:'#FFAB91',
				exercise:true,
				title:'动作'
			},
			children:[
				{
					path:'chest',
					name:'chest',
					component:PartList,
					meta:{
						appbar:'#FFAB91',
					}
				},
				{
					path:'back',
					name:'back',
					component:PartList,
					meta:{
						appbar:'#FFAB91',
					}
				},
				{
					path:'shoulder',
					name:'shoulder',
					component:PartList,
					meta:{
						appbar:'#FFAB91',
					}
				},
				{
					path:'arm',
					name:'arm',
					component:PartList,
					meta:{
						appbar:'#FFAB91',
					}
				},
				{
					path:'Abs',
					name:'Abs',
					component:PartList,
					meta:{
						appbar:'#FFAB91',
					}
				},
				{
					path:'buttocks',
					name:'buttocks',
					component:PartList,
					meta:{
						appbar:'#FFAB91',
					}
				},
				{
					path:'leg',
					name:'leg',
					component:PartList,
					meta:{
						appbar:'#FFAB91',
					}
				},
				{
					path:'body',
					name:'body',
					component:PartList,
					meta:{
						appbar:'#FFAB91',
					}
				},
				{
				path:'',
				component:PartList
				},
		]
			
		},
		{
			path:'/part/detail',
			name:'detail',
			component:PartDetail,
			meta:{
				appbar:'#FFAB91',
				exercise:true,
				title:'详情'
			}
			
		},
		
		{
			path:'/about',
			name:'about',
			component:MyAbout,
			meta:{
				appbar:'#BDBDBD',
				exercise:true,
				title:'关于'
			}
		},
		{
			path:'*',
         redirect:'/todo'
		},

	]
})